@import '../../../styles/';


.content {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;

    .navbar {
        display: flex;
        justify-content: space-between;
        align-items: start;
        width: 100%;
        padding-top: 12px;
        padding-left: 5%;
        padding-right: 5%;

        .option {
            display: flex;
            align-items: center;
            padding-left: 15px;
            padding-right: 15px;
            gap: 15px;
            text-decoration: none;
            font-size: 15px;
            letter-spacing: 1.5px;
            font-family: var(--font-family-text);
            color: var(--font-color-one);
            opacity: 0.8;
            transition: 0.7s;

            &:hover {
                text-shadow: 0px 0px 20px var(--basic-three);
                opacity: 1;
            }

            & span {
                height: 20px;
            }

            .icon {
                height: 19px;
                box-sizing: content-box;
                border-radius: 10%;
                opacity: 0.6;
                transition: 0.7s;

                &:hover {
                    filter: drop-shadow(0px 0px 5px var(--basic-two));
                    opacity: 1;
                }
            }

            .avatar {
                width: 20px;
                height: 20px;
                border-radius: 50%;
                object-fit: cover;
                object-position: center;
            }
        }
    }
}











